<template>
  <div class="cms-list-layout" style="padding:0px 16px;">
    <ul class="prodSearch-intro-ul clearfix">
      <li class="prodSearch-intro-li" v-for="(item) in tableData" :key="item.id">
        <div class="prodSearch-intro-box">
          <img :src="$cms.filterUrl(item.product.coverImage)" class="prodSearch-intro-box__img">
          <p class="prodSearch-intro-box__descript">
            {{item.product.name}}
          </p>
          <p class="prodSearch-intro-box__price error">
            ￥{{$cms.float(item.product.salePrice)}}
          </p>
        </div>
        <div class="prodSearch-toolbar" :key="item.id" v-if="signStatus<=0&&canShowAudit">
          <span class="el-icon-check" title="审核通过" @click="signCheck([item.id],'审核通过')"></span>
          <span class="el-icon-close" title="审核不通过"  @click="signCheck([item.id],'审核不通过',false)"></span>
        </div>
        <span class="prodSearch__aduit--scucc" v-if="item.status==1">已审核</span>
        <span class="prodSearch__aduit--fail" v-if="item.status==2">退回</span>
        <span class="prodSearch__aduit--primary" v-if="item.status==0">待审核</span>
      </li>

    </ul>
    <cms-pagination @change="getPage" :total='total' style="padding:10px 0"></cms-pagination>
  </div>
</template>
<script>
import {
  promoteProductPage,
  promoteSignCheck
} from './../../../api/marketing/index.js'
import list from '@/mixins/list'
var defaultData = {
  promoteId: '',
  id: '',
  size: 10,
  page: 1,
  storeId: ''
}
export default {
  mixins: [list],
  props: {
    promoteId: [String, Number],
    id: [String, Number],
    storeId: [String, Number],
    signStatus: [String, Number],
    canShowAudit: {// 店铺列表传过来的值(是否可以操作)
      type: Boolean
    }
  },
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    signCheck(singProductIds, reason, status = true) {
      // 审核商品
      promoteSignCheck({
        id: this.id,
        checked: status,
        singProductIds: singProductIds,
        reason: reason
      }).then(res => {
        if (res.code === 200) {
          this.$successMessage('审核成功')
          this.getTableData(promoteProductPage, this.queryParams)
        } else {
          this.$errorMessage(res.message)
        }
      })
    }
  },
  created() {
    this.queryParams.promoteId = this.promoteId
    this.queryParams.storeId = this.storeId
    this.getTableData(promoteProductPage, this.queryParams)
  }
}
</script>

<style lang='scss' scoped>
/* 商品搜索 */
.prodSearch-toolbar {
  width: 100%;
  height: 26px;
  border-top: 1px solid #e0e0e0;
  display: flex;
  justify-items: flex-start;
  span {
    flex: 1;
    height: 26px;
    line-height: 26px;
    text-align: center;
    &:first-child {
      border-right: 1px solid #e0e0e0;
    }
  }
}
/* 商品搜索主体 */
.prodSearch-intro {
  position: relative;
  font-size: 14px;
  overflow: hidden;
}
.prodSearch-intro-li {
  cursor: pointer;
  float: left;
  width: 150px;
  // height: 180px;
  border: 1px solid #e0e0e0;
  padding: 10px 0px 0 0px;
  margin-top: 10px;
  border-radius: 4px;
  margin-right: 15px;
  position: relative;
}
.prodSearch__aduit--scucc {
  font-size: 12px;
  display: block;
  position: absolute;
  top: 5px;
  left: 10px;
  padding: 0px 4px;
  border-radius: 3px;
  background: #5cb85c;
  color: #fff;
}
.prodSearch__aduit--fail {
  font-size: 12px;
  display: block;
  position: absolute;
  top: 5px;
  left: 10px;
  padding: 0px 4px;
  border-radius: 3px;
  background: #fe5331;
  color: #fff;
}
.prodSearch__aduit--primary{
    font-size: 12px;
  display: block;
  position: absolute;
  top: 5px;
  left: 10px;
 padding: 0px 4px;
  border-radius: 3px;
  background: #188ae2;
  color: #fff;
}

.prodSearch-intro-box {
  &__img {
    width: 80px;
    height: 80px;
    display: block;
    margin: 0px auto 5px auto;
  }
  &__price {
    text-align: center;
    font-size: 14px;
    color: #f56c6c;
    margin-bottom: 10px;
  }
  &__descript {
    text-align: center;
    font-size: 14px;
    color: #444;
    line-height: 20px;
    height: 40px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
  }
  &__cancel {
    display: none;
    height: 32px;
    background: rgba(0, 0, 0, 0.6);
    color: #fff;
    line-height: 32px;
    font-size: 14px;
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
}
.prodSearch-intro-li:hover {
  .prodSearch-intro-box__cancel {
    display: block;
  }
}
</style>




